<template>
 <div class="w-full h-screen bg-gradient-brand  mx-auto relative container">

  <div id="app">
    <app-header></app-header>
    <transition name="fade" mode="out-in">
      <router-view/>
    </transition>
  </div>
  
 </div>
</template>

<script>
import Header from './components/Header';


export default {
  
  name: 'app',
  created(){
    this.$store.dispatch("initTiles");    
    this.$store.dispatch('initGame');
  },
  components: {
     appHeader: Header
  },
  
}
</script>

<style>
@import url('https://cdn.jsdelivr.net/npm/tailwindcss/dist/tailwind.min.css');
@import url('https://cdnjs.cloudflare.com/ajax/libs/animate.css/3.5.2/animate.css');
#app, body {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color:#222;
   -moz-user-focus: ignore;
  -moz-user-select: none;
  -webkit-user-select: none;
  -khtml-user-select: none;
  -o-user-select: none;
  user-select: none;
   /* margin-top: 20px; */
  /* background: black; */
}
a{
  color: #222;
}
.fade-enter-active, .fade-leave-active {
  transition-property: opacity;
  transition-duration: .25s;
}

.fade-enter-active {
  transition-delay: .25s;
}

.fade-enter, .fade-leave-active {
  opacity: 0
}
</style>



